//====================================================================================================
.page {
	&__hero {
		@include adaptiveValue("padding-top", 90, 20);
		@include adaptiveValue("padding-bottom", 78, 30);
		position: relative;
		&::before {
			content: "";
			position: absolute;
			background-image: url(../img/bg-decor.svg);
			background-repeat: no-repeat;
			width: 100%;
			height: 100%;
			bottom: 15%;
			left: 0;
			z-index: -1;
		}
	}

	&__problems {
		@include adaptiveValue("padding-top", 100, 40);
		@include adaptiveValue("padding-bottom", 100, 40);
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #fff;
	}
	&__list {
		@include adaptiveValue("padding-top", 80, 40);
		@include adaptiveValue("padding-bottom", 80, 40);
	}

	&__fondation {
		@include adaptiveValue("padding-top", 107, 40);
		@include adaptiveValue("padding-bottom", 80, 40);
		background-color: #fff;
	}

	&__author-kurs {
		@include adaptiveValue("padding-top", 80, 40);
		@include adaptiveValue("padding-bottom", 80, 40);
	}

	&__result {
		@include adaptiveValue("padding-top", 80, 40);
		@include adaptiveValue("padding-bottom", 80, 40);
		background-color: #fff;
	}
}
.hero {
	&__container {
		display: flex;
		align-items: center;
		@media screen and (max-width: $mobile) {
			position: relative;
			flex-direction: column;
			justify-content: space-between;
		}
	}

	&__content {
		min-height: toRem(648);

		width: min-content;
		@media screen and (max-width: $mobile) {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
		}
	}

	&__title {
		font-weight: 700;
		line-height: 150%;
		@include adaptiveValue("font-size", 45, 20);
		color: #44464a;
		&:not(:last-child) {
			@include adaptiveValue("margin-bottom", 50, 20);
		}
	}

	&__modal {
		border-radius: toRem(30);
		padding: toRem(30) toRem(50);
		background: radial-gradient(
			201.65% 141.42% at 0% 0%,
			rgb(119, 183, 219) 30.728766322135925%,
			rgb(139, 204, 241) 100%
		);
	}

	&__info {
		position: relative;
		&::before {
			content: "";
			position: absolute;
			background: url(../img/bg-screen.png) no-repeat;
			background-size: 100%;
			background-position: center;
			width: 100%;
			height: 100%;
			z-index: -1;
		}
		justify-content: end;
		align-items: center;
		@media screen and (max-width: $mobile) {
			position: absolute;
			background-size: contain;
			background-size: 50%;
			//  top: 50%;
			//  left: 50%;
			//  transform: translate(-50%);
		}
	}
	.hero__info {
		display: flex;
		height: toRem(720);
		width: toRem(647);
	}
}
.hero-modal {
	&__title {
		font-weight: 700;
		@include adaptiveValue("font-size", 18, 12);
		color: #fff;
		&:not(:last-child) {
			margin-bottom: toRem(30);
		}
	}

	&__btn {
		border-radius: toRem(130);
		padding: toRem(21) toRem(123);
		font-weight: 600;
		@include adaptiveValue("font-size", 24, 18);
		background-color: #fff;
		color: #3c7ca0;
	}
}
.hero-info {
	&__item {
		&_1 {
			background: #ffdce8;
		}

		&_2 {
			background: #dee0ff;
		}

		&_3 {
			background: #ceebfc;
		}
	}
}

//====================================================================================================

.problems {
	&__container {
		row-gap: toRem(80);
		@media screen and (max-width: $tablet) {
			flex-direction: column;
		}
		&::before {
			content: "";
			position: absolute;
			background-image: url(../img/arrow.svg);
			background-repeat: no-repeat;
			width: toRem(100);
			height: toRem(100);
			left: 50%;
			top: 50%;
			z-index: 1;
			transform: translate(-50%, -50%);
			@media screen and (max-width: $tablet) {
				transform: translate(-50%, -50%) rotate(90deg);
			}
		}
	}

	&__items {
		width: 50%;
		@media screen and (max-width: $tablet) {
			width: 100%;
		}
	}
}
.problems-items {
	&__title {
		font-weight: 700;
		@include adaptiveValue("font-size", 30, 18);
	}

