	&__images {
		position: relative;
		overflow: hidden;
		background: #f3f9fd;
		border-radius: toRem(30);
		@include adaptiveValue("padding-top", 350, 215);
		@include adaptiveValue("padding-bottom", 65, 15);
		@include adaptiveValue("padding-right", 50, 15);
		@include adaptiveValue("padding-left", 60, 15);
		flex: 0 0 50%;
		&::before,
		&::after {
			content: "";
			position: absolute;
			background-repeat: no-repeat;
		}
		&::before {
