//===================================================================

.list {
	line-height: 150%;
	color: #44464a;
	position: relative;

	&::before,
	&::after {
		content: "";
		position: absolute;
		background-repeat: no-repeat;
		background-size: 100%;

		z-index: -1;
	}
	&::before {
		background-image: url(../img/circle-elements.svg);
		bottom: 0;
		left: -7%;
		width: 33.3%; //55%
		height: 70%;
	}
	&::after {
		background-image: url(../img/Layer.svg);
		bottom: 11%;
		right: 0;
		width: 18%;
		height: 47%;
	}

	&__container {
		display: flex;
		flex-direction: column;
	}

	&__title {
		display: flex;
		flex-direction: column;
		&:not(:last-child) {
			@include adaptiveValue("margin-bottom", 50, 30);
		}
	}

	&__items {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(360px, 1fr));
		grid-template-rows: auto;
		justify-items: center;
		column-gap: toRem(30);
		row-gap: toRem(20);
		&:not(:last-child) {
			@include adaptiveValue("margin-bottom", 50, 30);
		}
	}

	&__item {
	}

	&__btn {
		align-self: center;
	}
}

.list-item {
	border-radius: toRem(30);
	padding: toRem(30) toRem(25);
	@include adaptiveValue("width", 370, 290);
	// @include adaptiveValue("height", 220, 170);
	min-height: toRem(170);
	box-shadow: 0 toRem(10) toRem(70) 0 rgba(79, 146, 170, 0.1);
	background: #fff;
	display: flex;
	flex-direction: column;
	gap: toRem(10);

	&__content {
		display: flex;
		@include adaptiveValue("gap", 20, 15);
		align-items: center;
	}
	&__img {
		@include adaptiveValue("width", 70, 50);
		@include adaptiveValue("height", 70, 50);
		background-repeat: no-repeat;
		background-position: center;
		background-size: 100%;
		flex: 0 0 auto;

		&_1 {
			background-image: url(../img/advantages/01.svg);
			&-format {
				background-image: url(../img/formats/01.svg);
			}
		}
		&_2 {
			background-image: url(../img/advantages/02.svg);
			&-format {
				background-image: url(../img/formats/02.svg);
			}
		}
		&_3 {
			background-image: url(../img/advantages/03.svg);
			&-format {
				background-image: url(../img/formats/03.svg);
			}
		}
		&_4 {
			background-image: url(../img/advantages/04.svg);
			&-format {
				background-image: url(../img/formats/04.svg);
			}
		}
		&_5 {
			background-image: url(../img/advantages/05.svg);
			&-format {
				background-image: url(../img/formats/05.svg);
			}
		}
		&_6 {
			background-image: url(../img/advantages/06.svg);
			&-format {
				background-image: url(../img/formats/06.svg);
			}
		}
		&_7 {
			&-format {
				background-image: url(../img/formats/07.svg);
			}
		}
		&_8 {
			&-format {
				background-image: url(../img/formats/08.svg);
			}
		}
	}

	&__title {
		font-weight: 700;
		@include adaptiveValue("font-size", 20, 18);
	}

	&__descr {
		@include adaptiveValue("font-size", 16, 14);
		font-weight: 400;
		display: -webkit-box;
		-webkit-line-clamp: 3; // количество строк
		-webkit-box-orient: vertical;
		overflow: hidden;
	}
}

.fondation {
	&__container {
		row-gap: toRem(40);
		@media screen and (max-width: $mobile) {
			flex-direction: column;
		}
	}
	&__images {
		flex: 0 0 50%;
		margin: 0 auto;
		img {
			width: 67.3%;
			min-width: toRem(260);
			@include adaptiveValue("border-radius", 25, 30);
			box-shadow: 30px 30px 0px 0px rgba(237, 246, 252, 1);
			@media screen and (max-width: $mobile) {
				box-shadow: 20px 20px 0px 0px rgba(237, 246, 252, 1);
			}
		}
	}
}
.fondation-body {
	line-height: 150%;
	color: #44464a;

	&__title {
		font-weight: 700;
		@include adaptiveValue("font-size", 30, 18);
		&:not(:last-child) {
			margin-bottom: toRem(20);
		}
	}

	&__list {
		display: flex;
		flex-direction: column;
		gap: toRem(30);
		margin-top: toRem(40);
	}

	&__descr {
		font-weight: 400;
		@include adaptiveValue("font-size", 18, 14);
		&:not(:last-child) {
			@include adaptiveValue("margin-bottom", 30, 20);
		}
	}

	&__info {
		display: flex;
		flex-direction: column;
		gap: toRem(20);
		text-align: left;
	}

	&__subtitle {
		font-weight: 600;
		@include adaptiveValue("font-size", 20, 14);
	}
}

.fondation {
	&__author {
	}
}
.fondation-author {
	background-color: #edf6fc;
	padding: toRem(20) toRem(30);
	max-width: min-content;
	text-wrap: nowrap;
	border-radius: toRem(30);
	display: flex;
	flex-direction: column;
	gap: toRem(10);

	&__name {
		font-weight: 700;
		@include adaptiveValue("font-size", 20, 14);
		color: #44464a;
	}

	&__location {
		font-weight: 400;
		@include adaptiveValue("font-size", 18, 14);
		color: #44464a;
	}
}

.author-kurs {
	line-height: 150%;
	color: #44464a;

	&__container {
	}

	&__body {
		display: flex;
	}

	&__main-content {
		margin-top: toRem(30);
		z-index: 3;
	}

	&__img {
		position: relative;
		background-image: url(../img/bg-two-screen.png);
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		width: 100%;
		z-index: 3;
		&::before,
		&::after {
			position: absolute;
			content: "";
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
		}
		&::after {
			background-image: url(../img/circle-white.svg);
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			z-index: -1;
		}
	}

	&__content {
		z-index: 3;
		margin-top: toRem(40);
		display: flex;
		margin-top: 40px;
		flex-direction: column;
		align-items: flex-end;
	}

	&__title {
		font-weight: 700;
		@include adaptiveValue("font-size", 30, 20);
		&:not(:last-child) {
			margin-bottom: toRem(10);
		}
	}

	&__full-name {
		&:not(:last-child) {
			@include adaptiveValue("margin-bottom", 30, 20);
		} // remove
	}

	&__marker {
	}

	&__line {
		position: relative;
		margin-top: toRem(50);
		margin-bottom: toRem(50);
		&::before {
			content: "";
			position: absolute;
			background-image: url(../img/lines.svg);
			background-repeat: no-repeat;
			width: 100%;
			height: toRem(52);
		}
	}

	&__btn {
	}

	&__think {
		border-radius: toRem(30);
		box-shadow: 0 10px 70px 0 rgba(79, 146, 170, 0.1);
		background: #fff;
		padding: toRem(50) toRem(30);
		font-style: italic;
		font-weight: 400;
		@include adaptiveValue("font-size", 18, 14);
		&:not(:last-child) {
			@include adaptiveValue("margin-bottom", 50, 30);
		}
		max-width: toRem(370);
	}

	&__info {
	}

	&__list {
		&:not(:last-child) {
			margin-bottom: toRem(50);
		}
	}

	&__item {
		background: #fff;
		p {
			margin-left: toRem(45);
		}

		&_1 {
			position: relative;
			&::before {
				content: "";
				position: absolute;
				background: url(../img/icons/hat.svg) no-repeat;
				width: toRem(34);
				height: toRem(34);
				top: 50%;
				left: toRem(30);
				transform: translate(0, -50%);
			}
		}

		&_2 {
			position: relative;
			&::before {
				content: "";
				position: absolute;
				background: url(../img/icons/certificate.svg) no-repeat;
				width: toRem(40);
				height: toRem(40);
				top: 50%;
				left: toRem(30);
				transform: translate(0, -50%);
			}
		}
	}
	&__descr {
		display: flex;
		gap: toRem(30);
	}
}

.author-kurs-descr {
	margin-top: toRem(150);

	&__title {
		font-weight: 700;
		@include adaptiveValue("font-size", 30, 24);
		line-height: 150%;
		color: #44464a;
		@include adaptiveValue("margin-bottom", 40, 20);
		gap: toRem(10);
		p {
			text-wrap: nowrap;
		}
	}

	&__descr {
		font-weight: 400;
		@include adaptiveValue("font-size", 18, 16);
	}
}

.result {
	&__container {
		display: flex;
		gap: toRem(70);
	}

	&__images {
		position: relative;
		background-color: #edf6fc;
		border-radius: toRem(25);
		width: toRem(500);
		height: toRem(730);
		background-image: url(../img/result-done-imgsvg.svg);
		max-width: toRem(500);
		flex: 1 0 50%;
	}

	&__body {
		margin-top: toRem(30);
	}
}
.result-body {
	&__title {
		font-weight: 700;
		@include adaptiveValue("font-size", 30, 24);
		line-height: 150%;
		display: flex;
		flex-direction: column;
		gap: toRem(10);
		@include adaptiveValue("margin-bottom", 40, 20);
		p {
			@include adaptiveValue("font-size", 80, 50);
		}
	}

	&__items {
		font-weight: 600;
		@include adaptiveValue("font-size", 18, 16);
		line-height: 150%;
		color: #44464a;
		@include adaptiveValue("margin-bottom", 50, 30);
		display: flex;
		flex-direction: column;
		@include adaptiveValue("gap", 30, 20);
		li {
			list-style-type: disc;
			list-style-image: url(../img/marker-blue.svg);
		}
		// li::marker {
		//       // color: red;

		//       background-image: url(../img/marker-blue.svg);
		// }
	}

	&__item {
		padding-left: toRem(10);
	}

	&__btn {
	}
}
